<template>
  <button class="fullScreen-btn" @click="fullScreen"></button>
</template>

<script lang="ts" setup>
// three
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { GUI } from "three/examples/jsm/libs/lil-gui.module.min.js";

// 
const scene = new THREE.Scene();

// 
const camera = new THREE.PerspectiveCamera(
  45, // ()
  window.innerWidth / window.innerHeight, // 
  0.1, // 
  1000 // 
);

// 
const renderer = new THREE.WebGLRenderer();
renderer.setSize(window.innerWidth, window.innerHeight);
document.body.appendChild(renderer.domElement);

// 
const geometry = new THREE.BoxGeometry(1, 1, 1);

// 
const parentMaterial = new THREE.MeshBasicMaterial({ color: 0xff0000f });
const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });

// 
const parentCube = new THREE.Mesh(geometry, parentMaterial);
const cube = new THREE.Mesh(geometry, material);
parentCube.add(cube);
parentCube.position.set(-3, 0, 0); // position

cube.position.set(2, 0, 0);
// cube
cube.scale.set(2, 2, 2); // scale

// 
scene.add(parentCube);

// 
camera.position.z = 5;
camera.position.y = 2;
camera.position.x = 5;
camera.lookAt(0, 0, 0); // 

// 
const axesHelper = new THREE.AxesHelper(5); // 
scene.add(axesHelper);

// 
const controls = new OrbitControls(camera, renderer.domElement);
// 
controls.enableDamping = true;
// 
controls.dampingFactor = 0.01;
// 
controls.autoRotate = true;

// 
function animate() {
  controls.update();
  requestAnimationFrame(animate);
  cube.rotation.x += 0.01;
  cube.rotation.y += 0.01;
  // 
  renderer.render(scene, camera);
}
animate();

// 
window.addEventListener("resize", () => {
  renderer.setSize(window.innerWidth, window.innerHeight);
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
});

// 
function fullScreen() {
  renderer.domElement.requestFullscreen(); // 
}

// 
const eventObj = {
  Fullscreen() {
    document.body.requestFullscreen();
  },
  exitFullscreen() {
    document.exitFullscreen(); // document
  },
};

// GUI
const gui = new GUI();
// 
gui.add(eventObj, "Fullscreen").name("");
gui.add(eventObj, "exitFullscreen");
</script>
<style scoped>
.fullScreen-btn {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 999;
}
</style>
